<template>
  <div class="register-wrapper">
    <form class="register-form" v-on:submit.prevent="register">
      <input type="text" id="email" v-model="email" placeholder="Email" />
      <input
        type="text"
        id="password"
        v-model="password"
        placeholder="Password"
      />
      <input type="submit" value="Register" />
      <transition name="fade-right">
        <section v-if="errorMsg" class="errorMsg">
          <p>{{ errorMsg }}</p>
        </section>
      </transition>
    </form>
  </div>
</template>

<script>
import firebase from 'firebase/app';
export default {
  name: "register",
  data() {
    return {
      email: "",
      password: "",
      errorMsg: false,
    };
  },
  methods: {
    register() {
      firebase
        .auth()
        //register new user
        .createUserWithEmailAndPassword(this.email, this.password)
        // .then((account) => {
        .then(() => {
          // alert(`Created an account as ${account.user.email}`);
          //re-route to homepage
          this.$router.push("/");
        })
        .catch((err) => {
          this.errorMsg = err;
        });
    },
  },
};
</script>

<style scoped>
.register-wrapper {
  width: 100%;
  height: 100%;
}
.register-form {
  display: grid;
  grid-template-rows: 1fr 1fr 1fr;
}
.register-form input {
  padding: 1em;
  color: white;
  border-bottom: 1px solid white;
}
.register-wrapper div {
  width: 100%;
  height: 100%;
  padding: 1em;
}
.register-form input:nth-last-of-type(1) {
  color: grey;
}
.register-form input:nth-last-of-type(1):hover {
  color: white;
}
</style>
